import {Element} from "../Element"
import SwiperCore, { Pagination, A11y, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import { useEffect, useState } from "react";
import { EPIC_ELEMENTS } from "../../constant";
SwiperCore.use([Pagination, A11y, Autoplay])
const MAX_ELEMENT = 5

export const ListElement = () => {
  const [currentElement, setCurrentElement] = useState(0)
  const [items, setItems] = useState(EPIC_ELEMENTS[0].items)
  const [currentName, setCurrentName] = useState(EPIC_ELEMENTS[0])
  // useEffect(() => {
  //   new Swiper('.elementSwiper', {
  //     slidesPerView: 1,
  //     spaceBetween: 30,
  //     loop: true,
  //     autoplay: {
  //       delay: 5000,
  //       disableOnInteraction: false
  //     },
  //     breakpoints: {
  //       576: {
  //         slidesPerView: 3
  //       },
  //       1200: {
  //         slidesPerView: 5
  //       }
  //     },
  //     navigation: {
  //       nextEl: ".swiper-button-next",
  //       prevEl: ".swiper-button-prev",
  //     }
  //   })
  // }, [])
  useEffect(() => {
    setItems([])
    setCurrentName({})
    setTimeout(() => {
      setItems(EPIC_ELEMENTS[currentElement].items)
      setCurrentName(EPIC_ELEMENTS[currentElement])
    }, 500)
  }, [currentElement])
  const next = () => {
    const _nextCurrentElement = currentElement + 1
    setCurrentElement(_nextCurrentElement > MAX_ELEMENT ? 0 : _nextCurrentElement)
  }
  const prev = () => {
    const _prewCurrentElement = currentElement - 1
    setCurrentElement(_prewCurrentElement < 0 ? 0 : _prewCurrentElement)
  }
  return (
    <div className="section element" style={{
      background: `url(${EPIC_ELEMENTS[currentElement].bg ? EPIC_ELEMENTS[currentElement].bg : ''}) center bottom/cover no-repeat`,
    }}>
    <div className="section__background"></div>
    <div className="containner">
      <div className="row">
        <div className="col-lg-10 col-12 mx-auto">
          <div className="section__content text-center">
            <div className="section__content-title">The Epic Elements</div>
          </div>
        </div>
        <div className="col-12">
						<div className="swiper element-tab elementSwiper">
              <Swiper
                slidesPerView= {1}
                spaceBetween= {30}
                loop={true}
                autoplay= {{
                  delay: 5000,
                  disableOnInteraction: false
                }}
                breakpoints= {{
                  576: {
                    slidesPerView: 3
                  },
                  1200: {
                    slidesPerView: 5
                  }
                }}
              >
							<div className="swiper-wrapper">
                { EPIC_ELEMENTS.map((item, index) => {
                  return (
                    <SwiperSlide>
                    <div className="swiper-slide" onClick={() => {setCurrentElement(index)}}>
                    <div className={`element__item ${index === currentElement ? 'active' : ''}`}>
                      <div className="element__item-image">
                        <img src={item.icon} alt="element-item" className="mx-auto"/>
                      </div>
                      <div className="element__item-content">{item.title}</div>
                    </div>
                  </div>
                  </SwiperSlide>
                  )
                }) }
							</div>
              </Swiper>
						</div>
            <div onClick={next} className="swiper-button-next"></div>
              <div className="element-info">
           
                <div className="left-info">
                {currentName.id ?
                  (
                  <>
                    <div className="left-info__name" data-aos="fade-right">{currentName.name ? currentName.name : ''}</div>
                      <div className="left-info__line" data-aos="fade-right">
                        <div className="left-info__title" data-aos="fade-right" data-aos-delay="1000">{currentName ? currentName.title : ''}</div>
                        <div className="left-info__stars">
                        <span></span>
                      </div>
                    </div>
                  </>
                  )
                 : null}
            
                </div>
                <Element id={currentElement}/>
                <div className={`right-info ${EPIC_ELEMENTS[currentElement].class}`}>
                  <div className="right-info__line"></div>
                  <div className="right-info__items">
                    {items && items.map((item, index) => {
                      return (
                        <div className="item">
                          <div className="item__icon"></div>
                          <div className="item__name" data-aos="fade-left" data-aos-delay={`${index * 1000}`}>{item}</div>
                      </div>
                      )
                    })}
                  </div>
                  {/* <div className="right-info__item">Spirit Focus</div>
                  <div className="right-info__item">Spirit Focus</div>
                  <div className="right-info__item">Spirit Focus</div> */}
                </div>
              </div>
							<div onClick={prev} className="swiper-button-prev"></div>
					</div>
      </div>
    </div>
  </div>
  )
}